.header {
  width: 100%;
  height: 300px;
  padding-top: 27px;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
  background-image: url("../img/PhotoFilter.jpg");
  background-size: cover;
  background-position: center;
}
.header__menu {
  display: flex;
  justify-content: space-between;
}
.header__button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.header__button:hover {
  background-color: #fcff19;
  border-radius: 10px;
  transition: background 0.5s;
}
.header__button:hover .header__button-svg {
  fill: #000000;
}
.header__button-svg {
  width: 30px;
  height: 40px;
  fill: #ffffff;
}
body {
  margin: 0;
  padding: 0;
}
.container {
  width: 300px;
  margin: 0 auto;
}
.visually-hidden,
.visually_hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
